import React, { useState, useCallback, useEffect } from "react";
import PropTypes from "prop-types";
import { useDisclosure } from "@mantine/hooks";
import { Modal, Button } from "@mantine/core";
import { Card, Image, Text, Badge, Group } from "@mantine/core";
import "./PokemonModal.css";

function PokemonModal({ opened, close, pokemon }) {
  const [isLoading, setIsLoading] = useState(true);
  const primaryType = pokemon?.types?.[0]?.type?.name;
  const secondaryType = pokemon?.types?.[1]?.type?.name;
  const getColorByType = (type) => {
    // Implement your logic to map types to colors (e.g., switch statement, object lookup)
    // Example:
    switch (type) {
      case 'fire':
        return 'red';
      case 'water':
        return 'blue';
      case 'grass':
        return 'green';
      case 'electric':
        return 'yellow';
      case 'psychic':
        return 'purple';
      case 'ice':
        return 'cyan';
      case 'dragon':
        return 'orange';
      case 'dark':
        return 'black';
      case 'fairy':
        return 'pink';
      case 'normal':
        return 'gray';
      case 'fighting':
        return 'brown';
      case 'flying':
        return 'skyblue';
      case 'poison':
        return 'purple';
      case 'ground':
        return 'brown';
      case 'rock':
        return 'gray';
      case 'bug':
        return 'chartreuse';
      default:
        return 'gray'; // Or a default color
    }
  };
  return (
      <>
        <Modal opened={opened} onClose={close} centered>
          <Card shadow="sm" padding="xl" component="a" target="_blank">
            <Card.Section>
              <Image
                  className="img"
                  src={pokemon?.sprites.front_default}
                  height={280}
                  alt={pokemon?.name}
              />
            </Card.Section>

            <Group justify="space-between" mt="md" mb="xs">
              <Text fw={500}>{pokemon?.name}</Text>
              {primaryType && (
                  <>
                    <Badge color={getColorByType(primaryType)}>
                      {primaryType}
                    </Badge>
                    {secondaryType && (
                        <Badge color={getColorByType(secondaryType)}>
                          {secondaryType}
                        </Badge>
                    )}
                  </>
              )}
            </Group>

            <Text size="sm" c="dimmed">
              {pokemon?.stats.map((state) => (
                  <div>
                    {state.stat.name} : {state.base_stat}
                  </div>
              ))}
            </Text>
          </Card>
        </Modal>
      </>
  );
}
PokemonModal.propTypes = {
  opened: PropTypes.bool.isRequired,
  close: PropTypes.func,
  pokemon: PropTypes.object,
};
export default PokemonModal;
