import React, { useState, useCallback, useEffect } from "react";
import PropTypes from "prop-types";
import "./PokemonCard.css";
import PokemonModal from "../PokemonModal/PokemonModal";
import { useDisclosure } from "@mantine/hooks";
import { Modal, Button } from "@mantine/core";

function PokemonCard({ url }) {

  const [pokemon, setPokemon] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [opened, { open, close }] = useDisclosure(false);

  const getColorByType = (type) => {
    // Implement your logic to map types to colors (e.g., switch statement, object lookup)
    // Example:
    switch (type) {
      case 'fire':
        return 'red';
      case 'water':
        return 'blue';
      case 'grass':
        return 'green';
      case 'electric':
        return 'yellow';
      case 'psychic':
        return 'purple';
      case 'ice':
        return 'cyan';
      case 'dragon':
        return 'orange';
      case 'dark':
        return 'black';
      case 'fairy':
        return 'pink';
      case 'normal':
        return 'gray';
      case 'fighting':
        return 'brown';
      case 'flying':
        return 'skyblue';
      case 'poison':
        return 'purple';
      case 'ground':
        return 'brown';
      case 'rock':
        return 'gray';
      case 'bug':
        return 'chartreuse';
      default:
        return 'gray'; // Or a default color
    }
  };

  async function fetchData() {

    console.log("FEtch Data");
    let ani = await fetch(url);
    let d = await ani.json();
    setPokemon(d);
    setIsLoading(false);
  }

  useEffect(() => {
    fetchData();
  }, []);

  return (
      <div className="card" style={{backgroundColor: getColorByType(pokemon?.types[0]?.type?.name)}}>
        <div className="card-details" onClick={open}>
          <img src={pokemon?.sprites.front_default} alt={pokemon?.name}/>
          <div className="id">#{pokemon?.id}</div>
          <div className="name">{pokemon?.name}</div>
        </div>
        <PokemonModal opened={opened} close={close} pokemon={pokemon}/>
      </div>
  );
}

PokemonCard.propTypes = {
  url: PropTypes.string.isRequired,
};
export default PokemonCard;
